import React, { Component } from 'react';
import classes from './SideItem.css';
import Aux from '../../../../hoc/Aux/Aux';
import { connect } from 'react-redux';


class sideItem extends Component {


    state = {
        active: this.props.active
    }

    test = () => {
        this.props.clicked();
        this.setState(prevState => {
            return { active: !prevState.active };
        });
    }

    componentWillReceiveProps() {
        setTimeout(() => {

            if (this.props.selectAll === true) {
                this.setState(() => {
                    return { active: true };
                });
            }
            else if (this.props.selectAll === false) {
                this.setState(() => {
                    return { active: false };
                });
            }
            ;
        }, 10);

    }

    render() {
        let attachedClassesToItem;
        this.state.active
            ? attachedClassesToItem = [classes.SideItem, classes.SideItemActive]
            : attachedClassesToItem = [classes.SideItem];

        let itemToRender = (<Aux>
            <li style={{ borderColor: this.props.color, borderStyle: 'none none none solid', background: '#292927' }} className={attachedClassesToItem.join(' ')} onClick={this.test} >
                {this.props.name}
            </li >
        </Aux>)

        if (!this.state.active) {
            itemToRender = (<Aux>
                <li className={attachedClassesToItem.join(' ')} onClick={this.test} >
                    {this.props.name}
                </li >
            </Aux>)
        }

        return (

            itemToRender
        );
    }
}




const mapStateToProps = state => {
    return {
        selectAll: state.chartLine.selectAll,
    };
};

const mapDispatchToProps = dispatch => {
    return {};
};

export default connect(mapStateToProps, mapDispatchToProps)(sideItem);


