import * as actionTypes from '../actions/actionTypes';
import { updateObject } from '../../shared/utility';


const initialState = {
    chartData: [],
    up: '0123',
    selectAll: false
};




const fetchIngredientsFailed = (state, action) => {
    let teste = state.chartData;
    let foundItem = teste.findIndex(d => d === action.active);
    if (foundItem >= 0) {
        teste.splice(foundItem, 1);
    } else {
        teste.push(action.active);

    }
    return updateObject(state, { up: Date.now(), chartData: teste, selectAll: null });
};

const selectAllItems = (state, action) => {
    let teste = state.chartData;
    action.items.forEach((element) => {
        let foundItem = teste.findIndex(d => d === element);
        if (foundItem >= 0) {
            element.active = true;
        } else {

            teste.push(element);

        }
    });
    return updateObject(state, { up: Date.now(), chartData: teste, selectAll: true });
};


const selectNoneItems = (state, action) => {
    let teste = state.chartData;
    action.items.forEach((element) => {
        teste.splice(element, 1);
    });
    return updateObject(state, { up: Date.now(), chartData: teste, selectAll: false });
};



const reducer = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.FETCH_INGREDIENTS_FAILED: return fetchIngredientsFailed(state, action);
        case actionTypes.SELECT_ALL_ITEMS: return selectAllItems(state, action);
        case actionTypes.SELECT_NONE_ITEMS: return selectNoneItems(state, action);
        default: return state;
    }
};

export default reducer;


