import React, { Component } from 'react';
import Aux from '../../../hoc/Aux/Aux';
import classes from './SideDrawer.css';
import SideItem from './SideItem/SideItem';
import { connect } from 'react-redux';
import * as actions from '../../../store/actions/index';
import dummyData from '../../../store/dummy/dummyData';


let itemsArray = dummyData;
class SideDrawer extends Component {

    clickHandler = (id, active) => {
        this.props.onTest(id, active);
    };

    onSelectAll = () => {
        this.props.onSelectAllItems(dummyData);
    };

    onSelectNone = () => {
        this.props.onSelectNoneItems(dummyData);
    };


    render() {
        let i;
        for (i = 0; i < 5; i++) {
            this.props.onTest(itemsArray[i].id, itemsArray[i]);
        };


        let listItems = itemsArray.map(itemElement => (
            <SideItem
                key={itemElement.id}
                name={itemElement.label}
                active={itemElement.active}
                color={itemElement.borderColor}
                clicked={() => this.clickHandler(itemElement.id, itemElement)}
            />

        ));

        return (
            <Aux>
                <div className={classes.SideDrawer}>
                    <section className={classes.SideDrawerTop}>
                        <h1 className={classes.SideDrawerTitle}>Selecione assuntos abaixo:</h1>
                        <ul className={classes.SideDrawerList}>
                            <li className={classes.SideDrawerAction} onClick={this.onSelectAll}>
                                <div><svg height="20px" version="1.1" viewBox="0 0 20 20" width="20px" xmlns="http://www.w3.org/2000/svg"><title /><desc /><defs /><g fill="none" id="Page-1" stroke="none"><g fill="white" id="Core" transform="translate(-128.000000, -86.000000)"><g id="check-circle-outline" transform="translate(128.000000, 86.000000)"><path d="M5.9,8.1 L4.5,9.5 L9,14 L19,4 L17.6,2.6 L9,11.2 L5.9,8.1 L5.9,8.1 Z M18,10 C18,14.4 14.4,18 10,18 C5.6,18 2,14.4 2,10 C2,5.6 5.6,2 10,2 C10.8,2 11.5,2.1 12.2,2.3 L13.8,0.7 C12.6,0.3 11.3,0 10,0 C4.5,0 0,4.5 0,10 C0,15.5 4.5,20 10,20 C15.5,20 20,15.5 20,10 L18,10 L18,10 Z" id="Shape" /></g></g></g></svg></div>
                                <p>Marcar todos</p>
                            </li>
                            <li className={classes.SideDrawerAction} onClick={this.onSelectNone}>
                                <div><svg height="20px" version="1.1" viewBox="0 0 20 20" width="20px"><title /><desc /><defs /><g fill="none" id="Page-1" stroke="none"><g fill="white" id="Core" transform="translate(-2.000000, -380.000000)"><g id="remove-circle-outline" transform="translate(2.000000, 380.000000)"><path d="M5,9 L5,11 L15,11 L15,9 L5,9 L5,9 Z M10,0 C4.5,0 0,4.5 0,10 C0,15.5 4.5,20 10,20 C15.5,20 20,15.5 20,10 C20,4.5 15.5,0 10,0 L10,0 Z M10,18 C5.6,18 2,14.4 2,10 C2,5.6 5.6,2 10,2 C14.4,2 18,5.6 18,10 C18,14.4 14.4,18 10,18 L10,18 Z" id="Shape" /></g></g></g></svg></div>
                                <p>Desmarcar todos</p>
                            </li>
                        </ul>
                    </section>

                    <ul className={classes.SideDrawerList}>
                        {listItems}
                    </ul>
                </div>
            </Aux>
        );

    }
}

const mapDispatchToProps = dispatch => {
    return {
        onTest: (id, active) => dispatch(actions.fetchIngredientsFailed(id, active)),
        onSelectAllItems: (items) => dispatch(actions.selectAll(items)),
        onSelectNoneItems: (items) => dispatch(actions.selectNone(items)),
    };
};


export default connect(null, mapDispatchToProps)(SideDrawer);




